/*
登录页的样式
*/
.login {
    height: 100%;
    width: 100%;
    background: url("../images/login-bg.jpg") no-repeat;
    background-size: 100% 100%;
    position: fixed;
    top: 0;
    left: 0;
    overflow: hidden;
}

.login-mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;

}

.login-center {
    width: 100%;
    text-align: center;
}

.login-top {
    display: -ms-flexbox;
    display: flex;
    width: 5rem;
    margin: 0 auto;
}

.login-top p {
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.login-top p .one {
    display: inline-block;
    width: 0.15rem;
    height: 0.15rem;
    border: 2px solid #ffffff;
    border-radius: 0.15rem;
    margin-right: 0.1rem;
}

.login-top p .two {
    font-size: 0.30rem;
    color: white;
}

.login-btn {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    font-size: 0.38rem;
    background: rgba(114, 54, 21, 0.72);
    width: 5rem;
    height: 0.78rem;
    margin: 1rem auto;
    color: white;
    letter-spacing: 4px;
}

.login-input {
    display: block;
    background: rgba(0, 0, 0, 0);
    border: none;
    border-bottom: 1px solid white;
    width: 5rem;
    color: white;
    height: 0.5rem;
    text-align: center;
    margin: 0.5rem auto;
}

.login-input::-webkit-input-placeholder { /* WebKit browsers */
    color: white;
}

.login-input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    color: white;
}

.login-input::-moz-placeholder { /* Mozilla Firefox 19+ */
    color: white;
}

.login-input:-ms-input-placeholder { /* Internet Explorer 10+ */
    color: white;
}

/*
mainPage的样式

*/
.main-menu {
    height: 0.98rem;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    border-top: 1px solid #cccccc;
}

.main-menu a {
    -ms-flex: 1 1;
        flex: 1 1;
    text-align: center;
    color: #333;
    font-size: 0.30rem;
}

.main-menu a img {
    height: 0.3rem;
    width: 0.3rem;
    display: inline-block;
}

.selectedStyle {
    color: red;
}

/*HOME页的样式*/
.home {
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100%;
}

.home-content {
    -ms-flex: 1 1;
        flex: 1 1;
}

.homeTop {
    background-size: 100%;
    border-bottom: 1px solid #cccccc;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    overflow: scroll;

}

.nav {
    display: -ms-flexbox;
    display: flex;
    /*height: 0.98rem;*/
    -ms-flex-align: center;
        align-items: center;
    width: 160%;

}

.nav::-webkit-scrollbar {
    display: none;
}

.nav-list {
    display: inline-block;
    margin: 0 0.2rem;
    height: 100%;
    line-height: 0.90rem;
}

.is-active {
    color: #f4629b;
    border-bottom: 2px solid #f4629b;
}

.home-middle {
    -ms-flex: 1 1;
        flex: 1 1;
}

/*我的页面样式*/
.MyHome {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100%;
    background: #ffffff;
}

.my-home-top {
    -ms-flex: 1 1;
        flex: 1 1;
}

.my-home-logo {
    display: -ms-flexbox;
    display: flex;
    padding: 0.5rem 0.2rem;
    -ms-flex-align: center;
        align-items: center;
    background: #f9f9f9;
}

.my-home-logo img.logo {
    height: 1.2rem;
    width: 1.2rem;
    border-radius: 50%;
    display: -ms-inline-flexbox;
    display: inline-flex;
    margin-right: 0.3rem;

}

.my-home-logo div.name-right {
    -ms-flex: 1 1;
        flex: 1 1;
}
.my-home-logo .level{

    padding: 0 0.2rem;
    background: #dfdada;
    border-radius: 0.2rem;
}

.my-home-logo div.name-right p:first-child {
    font-size: 0.36rem;
    margin-bottom: 0.1rem;
}

.my-home-logo div.name-right p:last-child {
    font-size: 0.24rem;
}

.my-home-list {
    padding: 0 0.3rem;
}

.my-home-list li {
    list-style: none;
    border-bottom: 0.5px solid #cccccc;
    padding: 0.3rem 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
.my-home-list li p{
    -ms-flex: 1 1;
        flex: 1 1;
    font-size: 0.26rem;
}
.my-home-list li img{
    display: -ms-inline-flexbox;
    display: inline-flex;
    width: 0.15rem;
    height: 0.25rem;
}
.my-home-list li .yu{
    color: #f4629b;
    padding-right: 0.2rem;
}


